<template>
  <!-- <tables 
    :datanya="datatable" 
    @ambil="ambil($event)"
    :order="[]"
    /> -->
  <div class="font-times">
    <!-- <div class="tips float-right">
      <button
        type="button"
        @click="print1"
        v-show="pilih.name != 'taufik'"
        class="btn btn-sm btn-outline-danger"
      >
        <span class="typcn typcn-document-text"></span>
      </button>
      <span class="tipstextB">Print Satu</span>
    </div> -->
    <div style="overflow-x: auto; width: 100%" v-if="ready">
      <div class="container-fluid">
        <div class="row p-2">
          <div class="col-sm-1 text-center">
            <!-- <button
              type="button"
              class="btn ml-1 btn-sm btn-primary shadow-lg font-times"
              style="background: blue"
            >
              Tambah
            </button>
            <button
              type="button"
              class="btn ml-1 btn-sm btn-success shadow-lg font-times"
            >
              Edit
            </button>
            <button
              type="button"
              class="btn ml-1 btn-sm btn-danger shadow-lg font-times"
            >
              Hapus
            </button>
            <button
              type="button"
              class="btn ml-1 btn-sm btn-primary shadow-lg font-times"
              style="background: green"
            >
              Export
            </button> -->
          </div>
          <div class="col-sm-4 row">
            <!-- SELECTION  -->
            <div class="col-5">
            <select
              class="form-control"
              style="width: 80px; height: 30px; font-size: 12px"
              v-model="selected"
            >
              <option>10</option>
              <option>20</option>
              <option>50</option>
              <option>100</option>
              <option :value="1000000">Semua</option>
            </select>
            </div>
            <!-- SELECTION -->
            <div class="col-3">
            <download-excel :data="td">
                <button type="button" class="btn btn-sm btn-success">
                  <span class="typcn typcn-chart-bar"></span>Excel
                </button>
              </download-excel>
            </div>
            <!-- <div class="col-3"> -->
              <!-- <div class="tips">
                <button
                  type="button"
                  @click="print2"
                  class="btn btn-sm btn-outline-danger"
                >
                  <span class="typcn typcn-document-text"></span>
                </button>
                <span class="tipstextB">Print Laporan</span>
              </div> -->
            <!-- </div> -->
          </div>
          <div class="col-sm-4">
            <!-- SEARCH -->
            <div class="sm-form ">
                <input type="text" id="search" name="search" class="form-control form-control-sm" placeholder="Cari data ..." v-model="search" >
            </div>
            <!-- SEARCH -->
          </div>
        </div>
      </div>
      <!-- SCROLL UP -->
      <div
        style="width: 30px; position: absolute; background: transparent"
        :style="`height:$heightpx;`"
        class="bg-red-400"
      ></div>
      <!-- SCROLL UP -->
      <div
        class="table-responsive-sm font-times p-3"
        id="tablenya"
        style="box-shadow: 1px 4px 6px black; overflow: scroll"
        v-dragscroll
      >
        <template>
          <table
            class="table-print table-sm table-bordered table-striped table-hover table-print border-black"
            style="
              overflow-x: scroll;
              overflow-y: hidden;
              height: 80px;
              white-space: nowrap;
            "
          >
            <thead class="mdb-color" :style="{ background: thbackground }">
              <tr class="text-white" style="overflow: scroll; color: white">
                <th class :style="{ color: thcolor }">#</th>
                <th
                  scope="col"
                  class="th-print cursor-pointer uppercase"
                  style="font-size: 21px"
                  @click="sort(item)"
                  v-for="(item, index) in thnya"
                  :key="index + 'th'"
                  v-show="!less.includes(item)"
                >
                  <span :style="{ color: thcolor }" style="font-size: 13px">
                    {{ item }}
                    <template v-if="orderWith">
                      <span v-show="orderBy == item">
                        <span class="typcn typcn-arrow-down"></span>
                      </span>
                    </template>
                    <template v-else>
                      <span v-show="orderBy == item">
                        <span class="typcn typcn-arrow-up"></span>
                      </span>
                    </template>
                  </span>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index3) in td" :key="index3 + 'td'">
                <td>{{ index3 + temp1 + 1 }}</td>
                <no-ssr>
                  <v-touch
                    tag="td"
                    v-on:tap="
                      ambil(item, index3);
                      modal = true;
                      aksi = 'update';
                    "
                    class="cursor-pointer"
                    v-for="(item2, indexs) in thnya"
                    :key="indexs + 'th2'"
                    v-html="item[item2]"
                    v-show="!less.includes(item2)"
                  ></v-touch>
                  <!-- item[item2].split('-').length>2 && item[item2].indexOf('20')!=-1 ? format(item[item2]):item[item2]  -->
                </no-ssr>
              </tr>
            </tbody>
          </table>
        </template>
        <!-- FUNGSI DIBAWAH TABEL SEPERTI PAGINATION -->
        <div class="float-right">
          <template v-if="datanya.length >= selected">
            <button
              type="button"
              @click="pagPrev"
              class="btn btn-sm btn-outline-info roundec-circle"
            >
              Prev
            </button>
            &nbsp;
            <button
              type="button"
              @click="pagNext"
              class="btn btn-sm btn-info roundec-circle"
            >
              Next
            </button>
          </template>
          <template v-else>
            <button
              type="button"
              @click="pagPrev"
              class="btn btn-sm btn-black roundec-circle"
              disabled
            >
              Prev
            </button>
            &nbsp;
            <button
              type="button"
              @click="pagNext"
              class="btn btn-sm btn-black roundec-circle"
              disabled
            >
              Next
            </button>
          </template>
        </div>
      </div>
    </div>
    <div v-else>
      <p class="text-center">Loading Table ...</p>
    </div>
  </div>
</template>
<script>
var id = require("date-fns/locale/id");
import downloadExcel from "vue-json-excel";
import upload from "@/components/Single/UploadsNo.vue";
import axios from "axios";
export default {
  components: {
    upload,
    downloadExcel,
  },
  data() {
    return {
      // style
      height: 100,
      thbackground: "initial",
      thcolor: "asd",
      // valdatable
      vdata: {},
      ready: false,
      search: "",
      pilih: { name: "taufik", hobby: "coding" },
      thnya: {},
      err: false,
      checkedItem: [],
      less: [, "_id"],
      // modal
      modal: false,
      modalAction: "preview",
      // pagination
      selected: 10,
      pagination: 1,
      temp1: 0,
      temp2: 10,
      orderBy: "",
      orderWith: true,
      //form
      aksi: "insert",
      formOn: false,
      updateOn: false,
    };
  },
  methods: {
    refreshData() {
      let that = this;
      setTimeout(() => {
        (function () {
          var thElm;
          var startOffset;
          Array.prototype.forEach.call(
            document.querySelectorAll("table th"),
            function (th) {
              th.style.position = "relative";
              var grip = document.createElement("div");
              grip.innerHTML = "&nbsp;";
              grip.style.top = 0;
              grip.style.right = 0;
              grip.style.bottom = 0;
              grip.style.width = "5px";
              grip.style.position = "absolute";
              grip.style.cursor = "col-resize";
              grip.addEventListener("mousedown", function (e) {
                thElm = th;
                startOffset = th.offsetWidth - e.pageX;
              });
              th.appendChild(grip);
            }
          );
          document.addEventListener("mousemove", function (e) {
            if (thElm) {
              thElm.style.width = startOffset + e.pageX + "px";
            }
          });
          document.addEventListener("mouseup", function () {
            thElm = undefined;
          });
        })();
        let hasil = [];
        let obj = {};
        let kunci =
          that.order.length > 0 ? that.order : Object.keys(that.datanya[0]);
        that.datanya.forEach((e, index) => {
          kunci.forEach((e) => {
            obj[e] = that.datanya[index][e];
          });
          hasil.push(obj);
          obj = {};
        });
        that.datanya = hasil;
        let keys = kunci;
        keys = that.$_.difference(keys, that.unless);
        that.thnya = keys;
      }, 1000);
    },
    ambil(item, index) {
      window.scrollTo(0, 0);
      let that = this;
      this.updateOn = true;
      this.formOn = true;
      let clone = this.$_.clone(item);
      // clone["TGL_LHR"] = this.$datefns.format(
      //   new Date(clone["TGL_LHR"]),
      //   "YYYY-MM-dd"
      // );
      console.log(clone);
      this.vdata = clone;
      this.pilih = item;
      this.$emit("ambil", clone);
    },
    clearInput() {
      this.formOn = true;
      let keys = Object.keys(this.vdata);
      keys.forEach((key) => {
        this.vdata[key] = "";
      });
    },
    checkedAll() {
      console.log(this.checkedItem);
    },
    susun(datas) {
      let that = this;
      let data = datas;
      let order = this.order;
      let hasil = [];
      let obj = {};
      data.forEach((e, index) => {
        order.forEach((e) => {
          obj[e] = data[index][e];
        });
        hasil.push(obj);
        obj = {};
      });
      return hasil;
    },
    print1() {
      this.$router.push(`/laporan/perdata?less=$${this.less.toString()}`);
      this.$store.state.print2 = [];
      this.$store.state.print2.push(this.pilih);
      this.$store.state.print2 = this.susun(this.$store.state.print2);
      alert("print satu");
    },
    print2() {
      this.$router.push(`/laporan/print?less=$${this.less.toString()}`);
      let that = this;
      this.$store.state.print2 = this.susun(this.td);
      alert("print banyak");
    },
    isDate(value) {
      var dateFormat;
      if (toString.call(value) === "[object Date]") {
        return true;
      }
      if (typeof value.replace === "function") {
        value.replace(/^s+|s+$$/gm, "");
      }
      dateFormat = /(^d{1,4}[.|/|-]d{1,2}[.|/|-]d{1,4})(s*(?:0?[1-9]:[0-5]|1(?=[012])d:[0-5])ds*[ap]m)?$$/;
      return dateFormat.test(value);
    },
    // MODAL
    format(date) {
      return this.$datefns.format(date, "DD MMMM YYYY", {
        locale: id,
      });
    },
    distanceToNow() {
      return this.$datefns.distanceInWordsToNow(
        new Date(2015, 0, 1, 0, 0, 15),
        {
          locale: id,
          includeSeconds: true,
        }
      );
      //=> "3 days ago"
    },
    // DATATABLE
    pagPrev() {
      this.pagination = this.pagination - 1;
      if (this.pagination == 0) {
        this.pagination = 1;
      }
      this.temp2 = this.pagination * this.selected;
      this.temp1 = Math.ceil(this.temp2) - this.selected;
    },
    pagNext() {
      this.pagination = this.pagination + 1;
      if (this.pagination > this.bagi + 1) {
        this.pagination = Math.round(this.bagi);
      }
      this.temp2 = this.pagination * this.selected;
      this.temp1 = Math.ceil(this.temp2) - this.selected;
    },
    sort(item) {
      this.orderBy = item;
      this.orderWith = !this.orderWith;
    },
  },
  props: {
    order: {
      type: Array,
      required: false,
    },
    datanya: {
      type: Array,
      required: false,
      default: () => [],
    },
    unless: {
      type: Array,
      required: false,
      default: () => ["gone"],
    },
    date: {
      type: Array,
      required: false,
      default: () => ["ditanyakan"],
    },
  },
  watch: {
    datanya() {
      if (this.datanya.length > 0) {
        this.ready = true;
      }
    },
    selected() {
      this.temp2 = this.pagination * this.selected;
      this.temp1 = this.temp2 - this.selected;
    },
    search() {
      this.pagination = 1;
      this.temp2 = this.pagination * this.selected;
      this.temp1 = this.temp2 - this.selected;
    },
  },
  computed: {
    // DATATABLE
    bagi() {
      let data = this.datanya;
      data = data.length / 10;
      console.log(data);
      return data;
    },
    th() {
      let data = this.datanya;
      let keys = Object.keys(data[0]);
      keys = this.$_.difference(keys, this.unless);
      return keys;
    },
    td() {
      let that = this;
      let data = this.datanya;
      //! order fungsi
      if (this.orderBy.length > 0) {
        let ordernya = this.orderWith ? "asc" : "desc";
        data = this.$_.orderBy(data, [this.orderBy], [ordernya]);
      }
      //! search fungsi
      let keys = this.thnya;
      if (typeof keys.length == "undefined") {
      } else {
        data = data.filter((v, i, a) => {
          let hasil = false;
          keys.filter((key) => {
            if (typeof v[key] == "string") {
              if (
                v[key].toLowerCase().indexOf(this.search.toLowerCase()) != -1
              ) {
                hasil = true;
              }
            } else {
              if (v[key] != null) {
                if (v[key].toString().indexOf(this.search) != -1) {
                  hasil = true;
                }
              } else {
              }
            }
          });
          if (hasil) return v;
        });
      }
      //! cari shortcut contoh
      // data = data.filter((v, i, a) => {
      //   let hasil = false;
      //   console.log(v.id_kelas==that.carikelas)
      //     if(v['id_kelas']==this.carikelas){
      //       return v
      //     }else if(this.carikelas==''){
      //       return v
      //     }
      // });
      data = data.map((e) => {
        let obj = {};
        let kunci = Object.keys(e);
        kunci = that.$_.difference(kunci, that.unless);
        kunci.forEach((a, index) => {
          if (kunci.find((e) => e == that.date[index])) {
          } else {
            // obj[a] = 'tanggal'
          }

          obj[a] = e[a];
          // typeof e[a] != "string"
          //   ? e[a]
          //   : new Date(e[a]) !== "Invalid Date" && !isNaN(new Date(e[a]))
          //   ? that.isDate(e[a])
          //     ? that.format(e[a])
          //     : e[a]
          //   : e[a];
        });
        return obj;
      });
      data = data.slice(this.temp1, this.temp2);
      return data;
    },
  },
  mounted() {
    let that = this;
    setTimeout(() => {
      that.refreshData();
    }, 3000);
    setTimeout(() => {
      that.height = document.querySelector("#tablenya").offsetHeight;
    }, 5000);
  },
};
</script>